import React, { Component } from 'react'
import { AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import 'ag-grid-enterprise';

import axios from 'axios'

class World extends Component {

    constructor(props) {
        super(props);
        this.state = {
          columnDefs: [
            { headerName: "Country", field: "Country", sortable: true, filter: true},
            { headerName: "NewConfirmed", field: "NewConfirmed", sortable: true, filter: true},
            { headerName: "TotalConfirmed", field: "TotalConfirmed", sortable: true, filter: true},
            { headerName: "NewDeaths", field: "NewDeaths", sortable: true, filter: true},
            { headerName: "TotalDeaths", field: "TotalDeaths", sortable: true, filter: true},
            { headerName: "NewRecovered", field: "NewRecovered", sortable: true, filter: true},
            { headerName: "TotalRecovered", field: "TotalRecovered", sortable: true, filter: true},
            { headerName: "Updated Date", field: "Date", sortable: true, filter: true}],
        rowData: [],
        columnGlobeDefs: [
            { headerName: "NewConfirmed", field: "NewConfirmed", sortable: true, filter: true},
            { headerName: "NewDeaths", field: "TotalConfirmed", sortable: true, filter: true},
            { headerName: "TotalDeaths", field: "TotalDeaths", sortable: true, filter: true},
            { headerName: "NewRecovered", field: "NewRecovered", sortable: true, filter: true},
            { headerName: "TotalRecovered", field: "TotalRecovered", sortable: true, filter: true}],
            rowDataGlobe: []
        }
    }
    componentDidMount ()  {
       axios.get('https://api.covid19api.com/summary')
        .then(response => {
        this.setState({
            rowData: response.data.Countries,
            rowDataGlobe: response.data.Global
        })
    })
    .catch(error => {
        console.error(error)
    })
        
    }

    render() {

        const rowDataGlobe = this.state.rowDataGlobe
        return (
        <div>
            <div
            className="ag-theme-alpine"
            style={{
            height: '100px',
            width: '800px',
            align: 'left' }}
        >
            <AgGridReact
            columnDefs={this.state.columnGlobeDefs}
            rowData={[rowDataGlobe]}
            >
            </AgGridReact>
        </div>
        <div
            className="ag-theme-alpine"
            style={{
            height: '550px',
            width: '800px',
            align: 'left' }}
        >
            <AgGridReact
            columnDefs={this.state.columnDefs}
            rowData={this.state.rowData}
            >
            </AgGridReact>
        </div>
    </div>

        )
    }
}
export default World